body {
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 18px;
  line-height: 1;
  font-weight: 400;
  color: #ffffff;
}
a {
  color: inherit;
  text-decoration: none;
}
img {
  max-width: 100%;
}
p,
li {
  line-height: 1.2;
}
li {
  line-height: 24px;
}
p:last-child,
li:last-child {
  margin-bottom: 0;
}
.page-title {
  font-family: 'Manrope', sans-serif;
  font-size: 50px;
  font-weight: 600;
  line-height: 1;
  color: #201e23;
}
.header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 3;
}
.header__wrap {
  display: flex;
  align-items: flex-start;
  padding: 24px 0;
}
.header .logo {
  max-width: 120px;
}
.header__menu {
  display: flex;
  list-style: none;
  margin: 14px 114px 0 auto;
  padding: 0;
  font-family: 'Manrope', sans-serif;
  font-weight: 600;
  font-size: 16px;
}
.header__menu-item:not(:last-of-type) {
  margin-right: 74px;
}
.header__phone {
  font-size: 16px;
  font-weight: 600;
  font-family: 'Manrope', sans-serif;
  margin-top: 18px;
}
.header_page {
  position: static;
  background-color: #f0f2f8;
  box-shadow: 0px 0px 18px 0px rgba(0, 0, 0, 0.22);
}
.header_page .header__wrap {
  padding: 19px 0;
}
.header_page .header__menu {
  color: #201e23;
  margin: 18px 106px 0 auto;
}
.header_page .header__menu-item:not(:last-of-type) {
  margin-right: 61px;
}
.header_page .header__menu-item.current a {
  color: #ffffff;
}
.header_page .header__menu-item.current a:before {
  opacity: 1;
  border-top-left-radius: 12px;
  border-bottom-right-radius: 12px;
}
.header_page .header__menu a {
  position: relative;
  z-index: 2;
  color: #201e23;
  transition: 0.3s;
}
.header_page .header__menu a:before {
  content: '';
  position: absolute;
  top: -3px;
  left: -12px;
  width: 100%;
  height: 100%;
  background-color: #9a61a5;
  z-index: -1;
  border-top-left-radius: 0;
  border-bottom-right-radius: 0;
  width: calc(100% + 25px);
  height: calc(100% + 7px);
  pointer-events: none;
  opacity: 0;
  transition: 0.3s;
}
.header_page .header__menu a:hover {
  color: #ffffff;
}
.header_page .header__menu a:hover:before {
  opacity: 1;
  border-top-left-radius: 12px;
  border-bottom-right-radius: 12px;
}
.header_page .header__phone {
  color: #201e23;
  margin-top: 22px;
}
.footer {
  width: 100%;
  background-color: #1d1a1f;
  z-index: 2;
  color: #ffffff;
}
.footer__wrap {
  display: flex;
}
.footer__top {
  padding: 39px 0 29px;
  align-items: center;
  border-bottom: 1px solid #343136;
}
.footer .logo {
  max-width: 180px;
  margin-right: 48px;
}
.footer__menu {
  display: flex;
  padding: 0;
  margin: 0 0 12px;
  list-style: none;
  font-size: 20px;
  font-weight: 700;
  font-family: 'IBM Plex Sans', sans-serif;
}
.footer__menu-item:not(:last-of-type) {
  margin-right: 36px;
}
.footer__contacts .phone,
.footer__contacts .address {
  font-size: 12px;
  font-weight: 400;
  font-family: 'IBM Plex Sans', sans-serif;
}
.footer__contacts .phone:not(:last-of-type),
.footer__contacts .address:not(:last-of-type) {
  margin-right: 46px;
}
.footer .rights {
  padding: 22px 0;
  justify-content: space-between;
}
.footer .rights__item {
  font-size: 12px;
  font-family: 'IBM Plex Sans', sans-serif;
  font-weight: 400;
}
.footer .rights__item:nth-of-type(2) {
  margin-left: 100px;
}
.footer .rights__item:nth-of-type(2) a:not(:last-of-type) {
  margin-right: 70px;
}
.footer .rights__item.siluet {
  margin-left: auto;
}
.footer .rights__item.siluet a {
  position: relative;
}
.footer .rights__item.siluet a:before {
  content: '';
  position: absolute;
  top: -4px;
  left: -23px;
  width: 20px;
  height: 22px;
  background-image: url('../img/icons/logo_siluet.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.breadcrumbs {
  display: flex;
  color: #201e23;
  list-style: none;
  font-family: 'Manrope', sans-serif;
  font-weight: 700;
  font-size: 12px;
  margin: 0;
  padding: 26px 0 19px;
}
.breadcrumbs__item:not(:last-of-type) {
  position: relative;
  margin-right: 25px;
}
.breadcrumbs__item:not(:last-of-type):after {
  content: '';
  position: absolute;
  top: 12px;
  left: calc(100% + 9px);
  width: 8px;
  height: 1px;
  background-color: #201e23;
}
.about__main-text {
  color: #201e23;
  font-size: 17px;
  max-width: 970px;
  margin-top: 41px;
  padding-bottom: 60px;
}
.about__main-text p {
  line-height: 1.8;
}
.about .wysiwyg {
  color: #201e23;
  padding: 27px 0 99px;
  max-width: 965px;
}
.about .wysiwyg h2,
.about .wysiwyg h3 {
  font-weight: 500;
}
.about .wysiwyg h4,
.about .wysiwyg h5,
.about .wysiwyg h6 {
  font-family: 'Manrope', sans-serif;
  font-weight: 600;
}
.about .wysiwyg h2 {
  font-size: 40px;
}
.about .wysiwyg h3 {
  font-size: 30px;
  margin-top: 75px;
}
.about .wysiwyg h4 {
  font-size: 25px;
  margin-top: 68px;
}
.about .wysiwyg h5 {
  font-size: 20px;
  margin-top: 68px;
}
.about .wysiwyg h6 {
  font-size: 17px;
  margin-top: 64px;
}
.about .wysiwyg ol,
.about .wysiwyg ul,
.about .wysiwyg p {
  font-size: 17px;
  line-height: 1.6;
}
.about .wysiwyg ol {
  counter-reset: item;
  list-style: none;
  padding: 0;
}
.about .wysiwyg ol li {
  position: relative;
  padding-left: 40px;
}
.about .wysiwyg ol li:not(:last-of-type) {
  margin-bottom: 16px;
}
.about .wysiwyg ol li:before {
  content: counter(item);
  counter-increment: item;
  position: absolute;
  left: 0;
  top: -4px;
  background-color: #9a61a5;
  color: #ffffff;
  font-size: 17px;
  width: 30px;
  height: 30px;
  border-radius: 100px;
  text-align: center;
  line-height: 30px;
}
.about .wysiwyg ul {
  list-style: none;
  padding: 0;
}
.about .wysiwyg ul li {
  position: relative;
  padding-left: 40px;
}
.about .wysiwyg ul li:not(:last-of-type) {
  margin-bottom: 16px;
}
.about .wysiwyg ul li:before {
  content: '';
  position: absolute;
  top: 9px;
  left: 2px;
  background-color: #9a61a5;
  width: 6px;
  height: 6px;
  border-radius: 100px;
}
.about .wysiwyg ul.files {
  margin-top: -10px;
}
.about .wysiwyg ul.files li:before {
  content: '';
  position: absolute;
  background: none;
  background-image: url('../img/icons/file.png');
  width: 18px;
  height: 22px;
  background-size: 18px 22px;
  background-repeat: no-repeat;
  border-radius: 0;
  top: -1px;
  left: -3px;
}
.about .wysiwyg ul.files li a {
  text-decoration: underline;
}
.advantages {
  background-image: url('../img/about-advantages.png');
  background-size: cover;
  background-position: center center;
