.licenses__text {
  font-size: 13px;
  font-family: 'Manrope', sans-serif;
  line-height: 1.5;
}
.services {
  padding-bottom: 64px;
}
.services__main-text {
  color: #201e23;
  line-height: 1.7;
  max-width: 970px;
  margin-top: 39px;
}
.services__wrap {
  margin-top: 35px;
}
.services__item {
  border-top: 1px solid #dadce2;
}
.services__toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px 0 15px;
  cursor: pointer;
}
.services__toggle.active .services__name {
  color: #9a61a5;
}
.services__toggle.active .services__arrow {
  transform: rotate(-90deg);
}
.services__name {
  color: #201e23;
  position: relative;
  font-weight: 600;
  font-size: 17px;
  margin: 0;
  padding-left: 37px;
  transition: 0.3s;
}
.services__name .count {
  border-bottom-right-radius: 5px;
  border-top-left-radius: 5px;
