  font-size: 10px;
  font-weight: 700;
  background-color: #9a61a5;
  color: #ffffff;
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  display: block;
  position: absolute;
  left: 0;
  top: -2px;
}
.services__arrow {
  width: 30px;
  height: 30px;
  border-radius: 100px;
  background-color: #9a61a5;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: rotate(90deg);
  transition: 0.3s;
}
.services__content {
  display: none;
  padding: 19px 40px 49px;
